export function loginValidator(email, password) {
    const errors = [];

    if (!password) {
        errors.push('Password cannot be empty')
    }

    if (!email) {
        errors.push('Email cannot be empty')
    }

    if (email && !validateEmail(email)) {
        errors.push('Invalid Email')
    }

    return errors
}

function validateEmail(email) {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    return re.test(email);
}
export function signupValidator(email, password, confirmPassword, name, phone, age) {
    const errors = loginValidator(email, password)

    if (!name) {
        errors.push('Name cannot be empty')
    }
    if (!phone) {
        errors.push('Phone cannot be empty')
    }
    if (!age) {
        errors.push('Age cannot be empty')
    }

    if (password && confirmPassword && password != confirmPassword) {
        errors.push('Password and confirm password must match')
    }

    if (!confirmPassword) {
        errors.push('Confirm password cannot be empty')
    }

    return errors
}