import { useState } from "react";
import { TextField } from "@mui/material";
import { Close } from "@mui/icons-material";
import styles from "../styles/Home.module.scss";
import { Link, useNavigate } from 'react-router-dom'
import { loginValidator, signupValidator } from "../utils/login.utils";
import { axiosClient } from "../utils/axios.config.js"
import { useCookies } from 'react-cookie'
import { configConstants } from "../common/constants";

const cookieKey = configConstants.COOKIE_KEY

export default function Login() {
  let [state, setState] = useState({
    login: true,
    name: "",
    email: "",
    password: "",
    phone: "",
    confirmPassword: "",
    age: 0,
    gender: "Male",
    loginErrors: [],
  });
  const [cookies, setCookie, removeCookie] = useCookies([cookieKey]);

  let navigate = useNavigate();

  let handleInput = async (evt) => {
    const { email, password, confirmPassword, name, phone, age, login } = state

    await setState({
      ...state,
      [evt.target.name]: evt.target.value,
      ...evt.target.name !== "gender" && {
        loginErrors: login ?
          loginValidator(email, password, confirmPassword) :
          signupValidator(email, password, confirmPassword, name, phone, age)
      },
    })
  };

  let loginOrSignUp = async () => {

    const { name, email, password, confirmPassword, phone, age, gender, login, loginErrors } = state

    if (!login) {
      const errors = signupValidator(email, password, confirmPassword, name, phone, age)
      if (errors.length === 0) {
        try {
          let data = { name, email, password, confirmPassword, phone, age, gender };
          const signedUpUser = await axiosClient().post(`/users/signup`, data)
          console.log(signedUpUser)
        } catch (err) {
          console.log(err)
          if (err.message.includes('409')) {
            await setState({ ...state, loginErrors: ['Email Already Exist'] })
          }
        }
      } else {
        await setState({ ...state, loginErrors: errors })
      }
    }

    //signing in

    if (login) {
      const errors = loginValidator(email, password)
      if (errors.length === 0) {
        try {
          let data = { email, password, };
          const { data: loggedInUser } = await axiosClient().post(`/users/login`, data)
          Object.keys(loggedInUser).forEach((_key,_index)=>{
            setCookie(_key,loggedInUser[_key])
          })
          navigate({
            pathname: loggedInUser.isAdmin ? '/adminHome' : "/userHome",
            query: { email: loggedInUser.email },
          });
        }
        catch (err) {
          console.log(err)
          await setState({ ...state, loginErrors: ['Login Failed'] })
        }
      } else {
        await setState({ ...state, loginErrors: errors })
      }
    }
  }


  return (
    <div style={{ backgroundColor: "rgba(0,0,0,0.05)" }}>
      <div className={styles.backDiv}>
        <h1>
          TourCyber<span>DB</span>
        </h1>
        <Link to="homePage">
          <Close fontSize="large" />
        </Link>
      </div>
      <div className={styles.root}>
        <div className={styles.form}>
          <div className={styles.input}>
            <h1>{state.login ? "Login" : "SignUp"}</h1>
          </div>

          <TextField
            className={styles.input}
            onChange={async (e) => await handleInput(e)}
            placeholder="Enter Email"
            variant="outlined"
            label="Email"
            name="email"
          />
          <TextField
            className={styles.input}
            onChange={async (e) => await handleInput(e)}
            placeholder="Enter Password"
            variant="outlined"
            label="Password"
            name="password"
          // type="password"
          />

          {!state.login ? (
            <>
              <TextField
                className={styles.input}
                onChange={async (e) => await handleInput(e)}
                placeholder="Confirm Password"
                variant="outlined"
                label="Confirm Password"
                name="confirmPassword"
              // type="password"
              />
              <TextField
                className={styles.input}
                onChange={async (e) => await handleInput(e)}
                placeholder="Enter Name"
                variant="outlined"
                label="name"
                name="name"
              />
              <TextField
                className={styles.input}
                onChange={async (e) => await handleInput(e)}
                placeholder="Phone Number"
                variant="outlined"
                label="Phone"
                name="phone"
              />
              <TextField
                className={styles.input}
                onChange={async (e) => await handleInput(e)}
                placeholder="Age"
                variant="outlined"
                label="Age"
                name="age"
                type="number"
              />
              <div style={{ padding: "10px", margin: "5px" }}>

                <div onChange={handleInput}>
                  <label>Select Gender</label>
                  <input type="radio" value="Male" name="gender" /> Male
                  <input type="radio" value="Female" name="gender" /> Female
                </div>
              </div>
            </>
          ) : (
            <div></div>
          )}

          {state.loginErrors.map((each, index) => (
            <p style={{ color: "rgb(255,50,50)", fontSize: '1em' }} key={index}>{each}</p>
          ))}
          <button
            className={styles.submitButton}
            onClick={loginOrSignUp}
          >
            {state.login ? "Login" : "SignUp"}
          </button>
          <p
            onClick={async () =>
              await setState({
                ...state,
                login: !state.login,
                loginErrors: [],
              })
            }
          >
            {!state.login
              ? "Do you already have an account? click here to login"
              : "Not registered? Click here to register"}
          </p>
        </div>
      </div>
    </div>
  );
}
