import { NotificationsNone, Power } from '@mui/icons-material'
import { useNavigate } from "react-router-dom"
import { useCookies } from 'react-cookie'
import { configConstants } from '../../common/constants';

const cookieKey = configConstants.COOKIE_KEY

export default function Header({ styles, title }) {

  const [cookies, setCookie, removeCookie] = useCookies([cookieKey]);

  let navigate = useNavigate();
  return (
    <div>
      <header className={styles.header}>
        <h1>CyberTourDB</h1>
      </header>
      <div
        style={{
          height: "3px",
          width: "100%",
          backgroundColor: "rgb(42, 42, 71)",
        }}
      ></div>
      <div className={styles.headerUnder}>
        <p className={styles.pageTitle}>{title}</p>
        <div className={styles.iconsHolder}>
          <NotificationsNone />
          <Power
            onClick={() => {
              let confirmed = window.confirm(
                "Are you sure you want to log-out?"
              );
              if (confirmed) {
                for (let _key in cookies) {
                  removeCookie(_key, { path: '/' })
                }
                navigate("/login");
              }
            }}
          />
        </div>
      </div>
    </div>
  );
}
