import { useState, React, useEffect } from "react";
import { Edit, Delete, Close, SearchRounded } from "@mui/icons-material";
import { TextField } from "@mui/material";
import { configConstants } from "../../common/constants";
import { useCookies } from 'react-cookie'
import { axiosClient } from "../../utils/axios.config"

const baseURL = configConstants.API_ROOT

let TourCard = ({ props, styles, popup, deleteOne }) => {
  return (
    <div className={styles.adminTicketCard}>
      <div className={styles.textSide}>
        <p className={styles.cardTitle}>{props.title}</p>
        <p className={styles.cardDesc}>{props.desc}</p>
      </div>
      <div className={styles.cardIcons}>
        <Edit onClick={() => popup()} />
        <div style={{ height: "20px" }}></div>
        <Delete onClick={() => deleteOne()} />
      </div>
    </div>
  );
};

export default function ManageTickets({ styles }) {

  const uploadRequiredFields = ['title', 'desc', 'price', 'availability', 'duration', ' imageUrl']

  let [state, setState] = useState({
    tickets: [],
    isNewRecord: false,
    edit: false,
    userSearch: "",
    searchedResult: [],
    presentEdit: {
      index: 0,
      title: "",
      desc: "",
      price: 0,
      duration: 0,
      availability: 0,
      imageUrl: ''
    },
    uploadedImg: null,
  });

  const [cookies] = useCookies(['cookie-name'])

  let refreshTickets = () => {
    axiosClient()
      .get(`/tickets`)
      .then((res) => {
        setState({ ...state, edit: false, tickets: res.data.tickets });
      })
      .catch((err) => {
        if (err) console.log(err);
      });
  };

  useEffect(() => {
    refreshTickets();
  }, []);

  let filterList = (word) => {
    let output = [];
    state.tickets.forEach((e) => {
      if (
        word.trim().length !== 0 &&
        e.title.toLowerCase().includes(word.toLowerCase())
      )
        output.push(e);
    });
    setState({ ...state, userSearch: word, searchedResult: output });
  };

  let mapped =
    state.searchedResult.length > 0 ? state.searchedResult : state.tickets;

  let handleChange = (e) => {
    setState({
      ...state,
      presentEdit: {
        ...state.presentEdit,
        [e.target.name]: e.target.value,
      },
    });
  };

  let popup = async (index) => {
    await setState({
      ...state,
      edit: true,
      isNewRecord: false,
      presentEdit: { index: index, ...state.tickets[index] },
    });
  };

  let deleteOne = (id, index) => {
    let confirmed = window.confirm("are you sure you want to delete Ticket?");
    if (confirmed) {
      axiosClient(cookies['token']).post(`/tickets/delete`, { ticketId: id }, (res, err) => {
        if (res) console.log({ res });
        else if (err) console.log({ err });
      });
      refreshTickets();
    }
  };



  let save = async (isNewRecord) => {
    let uploadData = {}

    const reader = new FileReader();
    reader.readAsDataURL(state.uploadedImg)
    reader.onerror = (e) => {
      console.log('error converting image:', e)
    }
    reader.onload = async () => {
      uploadData['imageUrl'] = reader.result;

      const { title, desc, price, availability, duration } = state.presentEdit;
      const allFieldsFilled = title && desc && price && availability && duration

      if (allFieldsFilled) {
        uploadData = { ...uploadData, title, desc, price, availability, duration }
        if (isNewRecord) {
          axiosClient(cookies['token'])
            .post(`/tickets`, uploadData)
            .then((res) => console.log(res))
            .catch((err) => console.log(err));
          setState({
            ...state,
            tickets: [...state.tickets, state.presentEdit],
            presentEdit: {},
            edit: false,
            isNewRecord: false,
          });
        } else {
          await axiosClient(cookies['token'])
            .patch(`/tickets`, uploadData)
            .then((res) => console.log(res))
            .catch((err) => console.log(err));
          await setState({
            ...state,
            presentEdit: {},
            edit: false,
            isNewRecord: false,
          });
          await refreshTickets();
        }
      } else {
        alert("Fill all fields Please");
      }
    }


  };

  let imageUpload = (e) => {
    const reader = new FileReader();
    reader.readAsDataURL(state.uploadedImg)
    reader.onerror = (e) => {
      console.log('error converting image:', e)
    }
    reader.onload = async () => {
      setState({
        ...state,
        uploadedImg: e.target.files[0],
        presentEdit: { ...state.presentEdit, imageUrl: reader.result },
      });
    }

  };

  if (state.edit) {
    //editting on
    return (
      <div className={styles.editScreen}>
        <div className={styles.closeContain}>
          <Close
            onClick={() => {
              setState({ ...state, edit: false, presentEdit: {} });
            }}
          />
        </div>
        <div className={styles.inputDiv}>
          <div className={styles.imageUpload}>
            <img src={state.presentEdit.imageUrl} alt='' />
            <p></p>
            <input type="file" onChange={(evt) => imageUpload(evt)} />
          </div>
          {uploadRequiredFields.map((each, index) => {
            return (
              each.trim() !== 'imageUrl' &&
              <TextField
                key={index}
                placeholder={each}
                variant="outlined"
                label={each}
                className={styles.editInput}
                name={each}
                value={state.presentEdit[each]}
                onChange={(e) => handleChange(e)}
                type={
                  each === "price" ||
                    each === "availability" ||
                    each === "duration"
                    ? "number"
                    : "text"
                }
              />
            );
          })}
          <button
            onClick={() => {
              save(state.isNewRecord);
            }}
          >
            Save
          </button>
        </div>
      </div>
    );
  }
  //editting off
  else {
    return (
      <div className={styles.adminManageTicketsContainer}>
        <div className={styles.searchContain}>
          <button
            onClick={() =>
              setState({ ...state, edit: true, isNewRecord: true })
            }
            className={styles.addButton}
          >
            Add Ticket
          </button>
          <div className={styles.search}>
            <input
              value={state.userSearch}
              onChange={(e) => filterList(e.target.value)}
            />
            <SearchRounded className={styles.searchIcon} size="xl" />
          </div>
        </div>

        <div className={styles.manageTickets}>
          <div>
            {mapped.map((each, index) => (
              <TourCard
                key={index}
                styles={styles}
                props={each}
                popup={() => popup(index)}
                deleteOne={() => deleteOne(each._id, index)}
              />
            ))}
          </div>
        </div>
      </div>
    );
  }
}
