import { useEffect, useState } from "react";
import { useNavigate } from 'react-router-dom'
import { TextField } from "@mui/material"
import { Switch } from '@mui/material'
import { axiosClient } from "../../utils/axios.config";
import { useCookies } from 'react-cookie'
import { configConstants } from "../../common/constants";

let cookieKey = configConstants.COOKIE_KEY


export default function AccountSettings({ styles, props }) {
  const [cookies, setCookie, removeCookie] = useCookies([cookieKey])
  let [state, setState] = useState({
    fields: {
      name: "",
      oldPassword: "",
      newPassword: "",
      confirmPassword: "",
      phone: "",
    },
    token: "",
    showPassword: false,
    passwordError: "",
  });
  let navigate = useNavigate();

  let handleChange = async (evt) => {
    await setState({
      ...state,
      passwordError: "",
      fields: { ...state.fields, [evt.target.name]: evt.target.value },
    });
  };

  useEffect(() => {
    const fetchDetails = async () => {
      setState({
        ...state,
        fields: {
          ...state.fields,
          name: cookies['name'] || '',
          phone: cookies['phone'] || '',
        }
      })
    }

    fetchDetails()
  }, []);

  let deleteAccount = () => {
    if (
      state.fields.oldPassword === state.fields.confirmPassword &&
      state.fields.oldPassword !== "" &&
      state.fields.confirmPassword !== ""
    ) {
      let confirmed = window.confirm("Are you sure you want to delete account?");
      if (confirmed) {
        axiosClient(cookies['token'])
          .post(`/users/delete`, {
            email: cookies['email'],
            password: state.fields.oldPassword,
          })
          .then((res) => {
            navigate("/login");
            removeCookie(cookieKey)
            console.log(res);
          })
          .catch((err) => {
            alert("Account Delete Failed!, Check password");
            console.log(err);
          });
      }
    } else {
      setState({
        ...state,
        passwordError: "Password must match Confirm-Password",
      });
    }
  };

  let updateUser = () => {
    const { name, oldPassword, newPassword, phone, confirmPassword } = state.fields
    if (state.fields.newPassword !== state.fields.confirmPassword) {
      setState({ ...state, passwordError: "Passwords must same!" });
    } else if (
      name !== "" &&
      oldPassword !== "" &&
      newPassword !== "" &&
      confirmPassword !== "" &&
      phone !== ""
    ) {
      let newData = {
        name: state.fields.name,
        password: state.fields.oldPassword,
        newPassword: state.fields.newPassword,
        oldPassword: state.fields.oldPassword,
        phone: state.fields.phone,
      };
      axiosClient(cookies['token'])
        .patch(`/users`, newData)
        .then((res) => {
          console.log(res);
          alert("User Update!");
        })
        .catch((err) => console.log(err));
    }
  };

  return (
    <div className={styles.accountSettingContainer}>
      <div className={styles.accountSetting}>
        <div className={styles.input}>
          <label>Show passwords?</label>
          <Switch
            color="primary"
            checked={state.admin}
            onChange={async function () {
              await setState({
                ...state,
                showPassword: !state.showPassword,
              });
            }}
          />
        </div>
        {Object.keys(state.fields).map((e, index) => (
          <TextField
            className={styles.settingInput}
            placeholder={e}
            // variant="outlined"
            label={e}
            value={state.fields[e]}
            name={e}
            type={
              (e === "oldPassword" ||
                e === "confirmPassword" ||
                e === "newPassword") &&
                !state.showPassword
                ? "password"
                : "text"
            }
            onChange={(e) => handleChange(e)}
            key={index}
          />
        ))}
        <p style={{ color: "red" }}>{state.passwordError}</p>
      </div>
      <div className={styles.submitButtonContainer}>
        <button className={styles.submitButton} onClick={() => deleteAccount()}>
          Delete Account
        </button>
        <button className={styles.submitButton} onClick={() => updateUser()}>
          Update
        </button>
      </div>
    </div>
  );
}
