import styles from "../styles/Home.module.scss";
import { useState, useEffect } from "react";
import Aside from "./components/aside";
import Header from "./components/header";
import axios from "axios";
import Overview from "./userComponents/overview";
import AccountSettings from "./userComponents/accountSettings";
import ManageTickets from "./adminComponents/manageTickets";
import ViewUsers from "./adminComponents/viewUsers";
import { axiosClient } from "../utils/axios.config";
import { useCookies } from 'react-cookie'
import { configConstants } from "../common/constants";


const cookieKey = configConstants.COOKIE_KEY

export default function AdminHome() {
  let [whichComponent, setWhichComponent] = useState(1);
  let [state, setState] = useState({
    _id: "",
    name: "",
    email: "",
    phone: "",
    ActiveTickets: [],
    notifications: [],
    overview: {
      userCount: 0,
      locations: 0,
      ticketsSold: 0,
    },
    tickets: [],
    cartTickets: [],
  });


  const [cookies] = useCookies([cookieKey]);

  let getUserFiles = async () => {
    let fetched = [];
    await axios
      .all([
        axiosClient().get("/users"),
        axiosClient().get("/tickets"),
        axiosClient().get("/payments"),
      ])
      .then(
        axios.spread((...response) => {
          fetched = response;
        })
      );
    await setState({
      ...state,
      email: cookies['email'],
      tickets: fetched[1].data.tickets,
      overview: {
        userCount: fetched[0].data.data.length,
        locations: fetched[1].data.count,
        ticketsSold: fetched[2].data.count,
      },
    });
  };

  useEffect(() => { getUserFiles() }, []);


  let components = [
    <Overview styles={styles} key={0} props={state.overview} />,
    <ManageTickets styles={styles} key={1} />,
    <ViewUsers styles={styles} key={2} />,
    <AccountSettings styles={styles} key={3} props={state} />,
  ];

  return (
    <div className={styles.background}>
      <Aside
        styles={styles}
        methodArray={[
          {
            text: "OverView",
            method: (which) => {
              setWhichComponent(which);
            },
            whichComponent: whichComponent,
          },
          {
            text: "Manage Tickets",
            method: (which) => {
              setWhichComponent(which);
            },
            whichComponent: whichComponent,
          },
          {
            text: "View Users",
            method: (which) => {
              setWhichComponent(which);
            },
            whichComponent: whichComponent,
          },
          {
            text: "Account Settings",
            method: (which) => {
              setWhichComponent(which);
            },
            whichComponent: whichComponent,
          },
        ]}
      />

      <main style={{ margin: "0 0 0 20vw" }}>
        <Header styles={styles} title="Admin DashBoard" />

        <div style={{ paddingTop: "60px" }}>{components[whichComponent]}</div>
      </main>
    </div>
  );
}
