import { useEffect, useState } from "react";
import { Delete, ShoppingCart } from "@mui/icons-material";

let CartCard = ({ props, styles, remove }) => {
  return (
    <div className={styles.cartCard}>
      <span className={styles.title}>{props.title}</span>
      <div className={styles.priceAndIcon}>
        <span className={styles.price}>{props.price}</span>
        <Delete
          onClick={() => {
            remove();
          }}
        />
      </div>
    </div>
  );
};

export default function ManageCart({ styles, cartTickets, removeOne }) {

  const getTotalPrice = () => {
    return cartTickets.length ? cartTickets.map(each => +each.price).reduce((a, v) => a + v) : 0
  }

  return (
    <div className={styles.manageCart}>
      <div className={styles.headerCard}>
        <p>Cart</p>
        <ShoppingCart color='white' />
      </div>
      <div>
        {cartTickets.map((each, index) => {
          return (
            <CartCard
              key={index}
              styles={styles}
              props={each}
              remove={() => removeOne(index)}
            />
          );
        })}
      </div>
      <div className={styles.bottomCard}>
        <p>Total</p>
        <span className={styles.price}>{getTotalPrice()}</span>
      </div>
      <div className={styles.payRow}>
        <p>{cartTickets.length < 1 ? "Cart is Empty" : ""}</p>
        <button>Purchase Tickets</button>
      </div>
    </div>
  );
}
